.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
    color: red;
}
.dialogsItems .active{
    color: black;
}

.messages {
padding: 10px;
}

.messages .message{
    color: black;
    margin-left: -170px;
}

.messages .buttonPart{
    margin-top: 20px;
}

.buttons{
    display: inline-block;
    padding: 5px 4px;
    font-size: 15px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: black;
    background-color: white;
    border: black  1px solid;
    border-radius: 1px;
    margin-left: 300px;
  }

.buttons:hover {background-color: lightgray}

.buttons:active {
    background-color: lightgray;
    box-shadow: 0 1px #666;
    transform: translateY(4px);
}
.textModule {
    margin-left: 230px;

}